import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home'
import ToyDetails from './components/ToyDetails'
import Profile from './views/Profile'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/profile',
      name: 'profile',
      component: Profile
    },
    {
      path: '/toys/:toyId?',
      name: 'toyDetails',
      component: ToyDetails
    },
  ]
})
