<template>
  <section v-if="toy" class="details-container">
    <v-btn @click="backToList" color="info" class="back-btn">Back to list</v-btn>
    <v-layout>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-img
            class="card-image"
            src="https://cdn.vuetifyjs.com/images/cards/desert.jpg"
            aspect-ratio="2.75"
          ></v-img>

          <v-card-title primary-title>
            <div class="card-text">
              <h3 class="headline mb-0">{{ toy.name }}</h3>
              <div>Type: {{ toy.type }}</div>
              <div>Price: {{ toy.price }}</div>
              <div>In Stock: {{ toy.inStock? 'Yes' : 'No' }}</div>
            </div>
          </v-card-title>
        </v-card>
      </v-flex>
    </v-layout>
  </section>
</template>


<script>
export default {
  data() {
    return {
      toy: null
    };
  },
  created() {
    const { toyId } = this.$route.params;
    this.$store.dispatch({ type: "getToyById", toyId }).then(toy => {
      this.toy = toy;
    });
  },
  methods: {
      backToList() {
          this.$router.push('/')
      }
  }
};
</script>

<style>
.v-card {
  margin: 0 auto;
  margin-top: 100px;
  width: 400px;
}

.details-container {
  display:flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
}

.back-btn{
    width: 150px;
    margin: 0 auto;
    margin-top: 10px;
}
</style>

