<script>
import { Bar } from "vue-chartjs";
import VueCharts from "vue-chartjs";
export default {
  extends: Bar,
  data() {
    return {
      mapData: []
    };
  },
  created() {
    this.map;
  },
  computed: {
    map() {
      return this.$store.dispatch({ type: "loadToys" }).then(() => {
        this.mapData = this.$store.getters.getToysMapNumByYear;
        this.renderChart({
          labels: this.mapData[0],
          datasets: [
            {
              label: "Books per release year",
              backgroundColor: "#f87979",
              data: this.mapData[1],
            }
          ]
        });
      });
    }
  }
};
</script>
