<template>
  <section>
    <form class="filter-form" @submit.prevent="setFilterSort">
      <div class="filter-container">
        Filter By:
        <br />
        <input v-model="filterBy.name" placeholder="Toy name..." />
        <br />
        <select v-model="filterBy.toyType">
          <option value disabled selected>Toy type</option>
          <option value="educational">Educational</option>
          <option value="funny">Funny</option>
          <option value="adult">Adult</option>
        </select>
        <br />
        <label>
          <input type="checkbox" v-model="filterBy.inStock" />In Stock
        </label>
        <br />
      </div>
      <div class="sort-container">
        <select v-model="sortBy.sortValue">
          <option value disabled selected>Sort by</option>
          <option value="name">Toy Name</option>
          <option value="price">Price</option>
          <option value="type">Type</option>
          <option value="inStock">In Stock</option>
        </select>
        <select v-model="sortBy.sortOrder">
          <option value disabled selected>Order by</option>
          <option value="asc">Ascending</option>
          <option value="desc">Descending</option>
        </select>
      </div>
      <button @click.native="submit">Go</button>
      <button @click="resetList">Reset</button>
    </form>
  </section>
</template>


<script>
export default {
  data() {
    return {
      filterBy: {
        name: "",
        inStock: "",
        toyType: ""
      },
      sortBy: {
        sortValue: "",
        sortOrder: ""
      }
    };
  },
  methods: {
    setFilterSort() {
      this.$store.dispatch({ type: "setFilterSort", filterBy: this.filterBy, sortBy: this.sortBy });
    },
    resetList() {
      this.$store.dispatch({ type: "loadToys" });
    }
  }
};
</script>

<style>
form {
  display: flex;
}
select {
  background: white;
  border: 1px solid black;
  color: black;
  width: 150px;
}

input {
  border: 1px solid black;
}

button {
  border: 1px solid black;
  height: 30px;
  width: 60px;
  margin: 10px;
}

.filter-form {
  margin: 0 auto;
  width: max-content;
}

.sort-container {
  margin: 20px;
  justify-self: center;
  display: flex;
  flex-direction: column
}
</style>
