import axios from 'axios'

export default {
    query,
    getToyById,
    getToysByFilterSort
}

//TODOS: Make backend in NodeJS
function query() {
    return axios.get(_getUrl())
        .then(res => res.data)
}

function getToyById(toyId) {
    return axios.get(_getUrl() + `${toyId}`)
        .then(res => res.data)
}

// TODOS: take care of type + instock filter + sort order
function getToysByFilterSort(filterBy, sortBy) {
    const { name, toyType, inStock } = filterBy
    let searchString = `?name=${name}`
    if (toyType) searchString += `&type=${toyType}`
    if (inStock) searchString += `&inStock=${inStock}`
    const { sortValue, sortOrder } = sortBy
    if (sortValue) searchString += `&sort=${sortValue}`
    if (sortOrder) searchString += `&order=${sortOrder}`
    return axios.get(_getUrl() + searchString)
        .then(res => res.data)
}

// Private 

function _getUrl() {
    const BASE_URL = (process.env.NODE_ENV !== 'development')
        ? '/toys'
        : 'http://localhost:3000/toys';
    return BASE_URL
}