<template>
  <section>
    <v-layout>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-img
            class="card-image"
            src="https://cdn.vuetifyjs.com/images/cards/desert.jpg"
            aspect-ratio="2.75"
          ></v-img>

          <v-card-title primary-title>
            <div class="card-text">
              <h3 class="headline mb-0">{{ toy.name }}</h3>
              <div>Type: {{ toy.type }}</div>
              <div>Price: {{ toy.price }}</div>
              <div>In Stock: {{ toy.inStock? 'Yes' : 'No' }}</div>
              <span>Added at: {{ toy.createdAt | moment("from") }}</span>
            </div>
          </v-card-title>

          <v-card-actions>
            <v-btn flat color="green" @click="goToDetails">Details</v-btn>
            <!-- <v-btn flat color="orange">Edit</v-btn> -->
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </section>
</template>


<script>
export default {
  props: {
    toy: Object,
    required: true
  },
  methods: {
      goToDetails() {
          this.$router.push(`/toys/${this.toy._id}`)
      }
  }
};
</script>

<style>
.v-card {
  display: flex;
  flex-direction: column;
}

.card-text {
  /* margin: 0 auto; */
}

.card-image {
  min-height: 100px;
}
</style>
