import Vue from 'vue'
import Vuex from 'vuex'
import toyService from './services/toyService'

Vue.use(Vuex)

export default new Vuex.Store({
    strict: true,
    state: {
        toys: [],
        currToy: null
    },
    mutations: {
        setToys(state, { toys }) {
            state.toys = toys
        },
        setCurrToy(state, { toy }) {
            state.currToy = toy
        }
    },
    getters: {
        getToys(state) {
            return state.toys
        },
        getToysMapNumByYear(state) {
            let toysNumByYear = {}
            state.toys.forEach(toy => {
                if (!toysNumByYear[toy.createdAt.substring(0, 4)]) toysNumByYear[toy.createdAt.substring(0, 4)] = 0
                toysNumByYear[toy.createdAt.substring(0, 4)] += 1
            })
            let result = []
            result.push(Object.keys(toysNumByYear).map((key) => {
                return Number(key);
            }))
            result.push(Object.keys(toysNumByYear).map((key) => {
                return toysNumByYear[key];
            }))
            return result;
        }
    },
    actions: {
        loadToys({ commit }) {
            return toyService.query()
                .then(toys => commit({ type: 'setToys', toys }))
        },
        getToyById({ commit }, { toyId }) {
            return toyService.getToyById(toyId)
                .then(toy => {
                    commit({ type: 'setCurrToy', toy })
                    return toy;
                })
        },
        setFilterSort({ commit }, { filterBy, sortBy }) {
            return toyService.getToysByFilterSort(filterBy, sortBy)
                .then(toys => commit({ type: 'setToys', toys }))
        }
    }
})
