<template>
  <v-app>
    <v-toolbar app>
      <v-toolbar-title class="headline text-uppercase">
        <span><router-link to="/">Mister Toy</router-link></span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn @click="goToProfile">
        <span class="mr-2">Profile</span>
      </v-btn>
    </v-toolbar>

    <v-content>
      <router-view />
    </v-content>
    <ToyChat v-if="chat.isChatActive">
      <template v-slot:header>Welcome to our chat!</template>

      <template v-slot:content class="slot-content">
        <div v-for="(msg,idx) in chat.conversation" :key="idx">{{msg}}</div>
      </template>

      <template v-slot:footer>
        <form @submit.prevent="chatSend">
          <input class="chat-input" v-model="chat.userInput" placeholder="Enter message here..." />
          <button>Send</button>
        </form>
      </template>
    </ToyChat>
    <v-icon @click="toggleChat" large color="blue darken-2">chat</v-icon>
  </v-app>
</template>

<script>
import ToyChat from "./components/ToyChat";
export default {
  name: "App",
  created() {
    this.$store.dispatch({ type: "loadToys" });
    document.addEventListener("keyup", this.escapeKeyListener);
  },
  destroyed() {
    document.removeEventListener("keyup", this.escapeKeyListener);
  },
  data() {
    return {
      chat: {
        isChatActive: false,
        userInput: "",
        conversation: []
      }
    };
  },
  components: {
    ToyChat
  },
  methods: {
    toggleChat() {
      this.chat.isChatActive = !this.chat.isChatActive;
      if (this.chat.isChatActive) {
        let timeout = setTimeout(() => {
          this.chat.conversation.push("Support:  Hello...");
        }, 1000);
      }
    },
    chatSend() {
      this.chat.conversation.push("Me:  " + this.chat.userInput);
      this.chat.userInput = "";
      setTimeout(() => {
        this.chat.conversation.push("Support: OMG!");
      }, 1000);
    },
    escapeKeyListener: function(evt) {
      if (evt.keyCode === 27 && this.chat.isChatActive) {
        this.chat.isChatActive = false;
      }
    },
    goToProfile() {
      this.$router.push('/profile')
    }
  },
  computed: {
    showConversation() {
      return this.chat.conversation[this.chat.conversation.length - 1];
    }
  }
};
</script>

<style>
.v-icon {
  position: fixed;
  bottom: 25px;
  right: 25px;
  cursor: pointer;
}

.chat-input::placeholder {
  color: white;
}
</style>
